@charset "UTF-8";
@font-face {
  font-family: "PT Sans";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/ptsans.woff2");
}
@font-face {
  font-family: "PT Sans";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/ptsansbold.woff2");
}
img {
  width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  height: 100%;
}

.page__body {
  width: 100%;
  margin: 0;
  height: 100%;
  font-family: "PT Sans", "Arial", sans-serif;
  background-color: #f2f2f2;
}

.page__container {
  display: flex;
  flex-direction: column;
  width: 320px;
  margin: 0 auto;
  min-height: 100%;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .page__container {
    width: 768px;
  }
}
@media (min-width: 1200px) {
  .page__container {
    width: 1200px;
    box-shadow: 0 5px 15px 0 rgba(0, 1, 1, 0.2);
  }
}

.page__modal-container-mistake,
.page__modal-container-feedback {
  position: fixed;
  display: flex;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
}
.page__modal-container-mistake--close,
.page__modal-container-feedback--close {
  display: none;
}

.header {
  display: grid;
  position: relative;
  grid-template-columns: 1fr;
  width: 320px;
  margin: 0 auto;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .header {
    width: 768px;
  }
}
@media (min-width: 1200px) {
  .header {
    width: 1200px;
  }
}

.header__logo {
  position: absolute;
  grid-area: 1/1/2/2;
  justify-self: center;
  z-index: 1;
}

.nav {
  position: relative;
  display: flex;
  flex-direction: column;
  grid-area: 1/1/2/2;
  width: 100%;
  min-height: 56px;
}

.nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  width: 62px;
  height: 56px;
  background-color: transparent;
  cursor: pointer;
  border: none;
}
@media (min-width: 768px) {
  .nav__toggle {
    display: none;
  }
}

.nav--closed .nav__toggle::before {
  content: "";
  position: absolute;
  background: url("../img/sprite.svg#opened-button") no-repeat center;
  top: 17px;
  left: 20px;
  width: 23px;
  height: 22px;
}

.nav--closed .nav__toggle:hover::before {
  opacity: 0.6;
}

.nav--closed .nav__toggle:active::before {
  opacity: 0.3;
}

@media (max-width: 767px) {
  .nav--closed .nav__item {
    display: none;
  }
}

.nav--opened .nav__list {
  position: absolute;
  z-index: 1;
}

.nav--opened .nav__list::after {
  content: "";
  position: absolute;
  bottom: 0;
  z-index: 1;
  width: 100%;
  height: 7px;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
}
@media (min-width: 768px) {
  .nav--opened .nav__list::after {
    background: none;
  }
}

.header__logo--nojs {
  top: 225px;
}
@media (min-width: 768px) {
  .header__logo--nojs {
    top: 0;
  }
}

.nav--nojs .nav__toggle {
  display: none;
}

.nav--nojs .nav__list {
  position: static;
  min-height: 0;
}

.nav--nojs .nav__item {
  display: block;
}

.nav--opened .nav__toggle::before {
  position: absolute;
  content: "";
  background: url("../img/sprite.svg#closed-button") no-repeat center;
  top: 15px;
  left: 18px;
  width: 27px;
  height: 27px;
}

.nav--opened .nav__toggle:hover::before {
  opacity: 0.6;
}

.nav--opened .nav__toggle:active::before {
  opacity: 0.3;
}

.nav__list {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  text-align: center;
  width: 100%;
  padding: 0;
  margin: 0;
  list-style-type: none;
}
@media (min-width: 768px) {
  .nav__list {
    flex-direction: row;
  }
}
@media (min-width: 1200px) {
  .nav__list {
    width: 1200px;
  }
}

.nav__item {
  background-color: #81b3d2;
}
@media (max-width: 767px) {
  .nav__item:hover {
    background-color: #669ec0;
  }
  .nav__item:active {
    background-color: #5496bd;
  }
}
@media (min-width: 768px) {
  .nav__item {
    background-color: #ffffff;
  }
  .nav__item:nth-child(3n+3) {
    margin-left: auto;
  }
}

.nav__link {
  display: block;
  padding-top: 17px;
  padding-bottom: 12px;
  font-weight: 700;
  font-size: 18px;
  line-height: 1.4;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  border-bottom: 1px solid #678fa8;
  border-top: 1px solid #9ac2db;
}
@media (min-width: 768px) {
  .nav__link {
    width: 153px;
    padding-top: 18px;
    padding-bottom: 17px;
    text-align: center;
    font-weight: 700;
    font-size: 14px;
    line-height: 1.5;
    color: #000000;
    border: none;
  }
  .nav__link:hover {
    color: #81b3d2;
  }
  .nav__link:active {
    color: #000000;
    opacity: 0.3;
  }
  .nav__link--active {
    color: #766357;
  }
}
@media (min-width: 1200px) {
  .nav__link {
    width: 240px;
  }
}

.advantages {
  background-color: #ffffff;
}

.advantages__main-title {
  margin: 0;
  padding: 34px 24px 35px;
  font-weight: 700;
  font-size: 18px;
  line-height: 1.44;
  color: #000000;
  text-transform: uppercase;
  text-align: center;
}
@media (min-width: 768px) {
  .advantages__main-title {
    padding-top: 48px;
    padding-bottom: 29px;
    font-size: 21px;
    line-height: 1.23;
  }
  .advantages__main-title br {
    display: block;
  }
}
@media (min-width: 1200px) {
  .advantages__main-title {
    padding-top: 61px;
  }
}

.advantages__main-description {
  margin: 0;
  margin-bottom: 16px;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.5;
  color: #333333;
  text-transform: uppercase;
  text-align: center;
}
@media (min-width: 768px) {
  .advantages__main-description {
    margin: 0;
  }
  .advantages__main-description br {
    display: none;
  }
}

.advantages__list {
  margin: 0;
  padding: 0;
  counter-reset: list1;
  list-style-type: none;
}
@media (min-width: 768px) {
  .advantages__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}

.advantages__item {
  color: #000000;
  background-color: #eeeeee;
}
@media (min-width: 768px) {
  .advantages__item:nth-child(1), .advantages__item:nth-child(2) {
    grid-column: 1/-1;
  }
}
.advantages__item--town, .advantages__item--devil {
  background-color: #81b3d2;
}
@media (min-width: 768px) {
  .advantages__item--town, .advantages__item--devil {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}
.advantages__item--town {
  position: relative;
  padding-top: 16px;
}
@media (min-width: 768px) {
  .advantages__item--town {
    padding: 0;
    margin-top: 56px;
  }
}
@media (min-width: 1200px) {
  .advantages__item--town {
    margin-top: 44px;
  }
}
.advantages__item--town::after {
  content: "";
  position: absolute;
  background: url("../img/sprite.svg#spacebar") no-repeat center;
  top: 0;
  width: 320px;
  height: 37px;
}
@media (min-width: 768px) {
  .advantages__item--town::after {
    display: none;
  }
}

.advantages__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #000000;
  background-color: #eeeeee;
}
@media (min-width: 768px) {
  .advantages__container {
    min-height: 256px;
  }
}
.advantages__container--town {
  box-sizing: border-box;
  padding: 54px 18px 40px;
}
@media (min-width: 768px) {
  .advantages__container--town {
    padding: 52px 33px 31px;
  }
}
@media (min-width: 1200px) {
  .advantages__container--town {
    padding: 52px 55px 51px;
  }
}
.advantages__container--devil {
  box-sizing: border-box;
  padding: 37px 18px;
}
.advantages__container--devil br {
  display: none;
}
@media (min-width: 768px) {
  .advantages__container--devil {
    grid-column: 3/4;
    padding: 52px 33px 31px;
  }
}
@media (min-width: 1200px) {
  .advantages__container--devil {
    padding: 52px 50px 51px;
  }
  .advantages__container--devil br {
    display: block;
  }
}
.advantages__container--town, .advantages__container--devil {
  color: #ffffff;
  background-color: #81b3d2;
}
.advantages__container--square, .advantages__container--road, .advantages__container--tourist {
  padding: 25px 18px;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .advantages__container--square, .advantages__container--road, .advantages__container--tourist {
    padding: 52px 6px;
  }
}
@media (min-width: 1200px) {
  .advantages__container--square, .advantages__container--road, .advantages__container--tourist {
    padding: 57px 20px 36px;
  }
}
@media (min-width: 768px) {
  .advantages__container--road {
    padding: 41px 20px;
  }
}
@media (min-width: 1200px) {
  .advantages__container--road {
    padding: 35px 22px 34px;
  }
}

@media (min-width: 768px) {
  .advantages__card-container {
    grid-column: 2/4;
  }
}
@media (min-width: 768px) {
  .advantages__card-container--devil {
    grid-area: 1/1/2/3;
  }
}

.advantages__card-image {
  display: block;
}

.advantages__card-title {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0;
  margin-bottom: 17px;
  font-weight: 700;
  font-size: 18px;
  line-height: 1.44;
  text-transform: uppercase;
  text-align: center;
}
.advantages__card-title br {
  display: none;
}
.advantages__card-title::after {
  counter-increment: list1;
  content: "− №" counter(list1) " −";
  margin-top: 11px;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.5;
}
@media (min-width: 768px) {
  .advantages__card-title {
    font-size: 21px;
    line-height: 1.23;
    margin: 0;
    margin-bottom: 24px;
  }
  .advantages__card-title br {
    display: block;
  }
}

.advantages__description {
  margin: 0;
  padding: 0 14px;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.5;
  color: #333333;
  text-transform: uppercase;
  text-align: center;
}
.advantages__description br {
  display: none;
}
@media (min-width: 768px) {
  .advantages__description {
    padding: 0 27px;
  }
  .advantages__description br {
    display: block;
  }
}
@media (min-width: 1200px) {
  .advantages__description {
    padding: 0 36px;
  }
}

.advantages__card-description {
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.5;
  text-transform: uppercase;
  text-align: center;
}

.main-container {
  flex-grow: 1;
  width: 320px;
  margin: 0 auto;
  padding-bottom: 10px;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .main-container {
    width: 768px;
    padding-bottom: 0;
  }
}
@media (min-width: 1200px) {
  .main-container {
    width: 1200px;
    padding-bottom: 0;
  }
}
@media (min-width: 768px) {
  .main-container--catalog {
    padding-bottom: 36px;
  }
}
@media (min-width: 768px) and (min-width: 1200px) {
  .main-container--catalog {
    padding-bottom: 32px;
  }
}

.search {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #ffffff;
  width: 100%;
  box-sizing: border-box;
}

.search__title {
  padding-top: 42px;
  padding-bottom: 21px;
  margin: 0;
  font-weight: 700;
  font-size: 21px;
  line-height: 1.2;
  text-transform: uppercase;
  text-align: center;
  color: #000000;
}
@media (min-width: 768px) {
  .search__title {
    padding-top: 61px;
    padding-bottom: 33px;
    font-size: 30px;
    line-height: 0.86;
  }
}

.search__description {
  padding: 0 18px 32px;
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.5;
  text-transform: uppercase;
  text-align: center;
  color: #333333;
}
.search__description br {
  display: none;
}
@media (min-width: 768px) {
  .search__description {
    padding-bottom: 40px;
  }
  .search__description br {
    display: block;
  }
}
@media (min-width: 1200px) {
  .search__description {
    padding-bottom: 45px;
  }
}

.search__button {
  display: block;
  padding: 16px 49px;
  margin-bottom: 49px;
  font-family: inherit;
  font-weight: 700;
  font-size: 21px;
  line-height: 1.2;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  background-color: #81b3d2;
}
.search__button:hover {
  background-color: #669ec0;
}
.search__button:active {
  background-color: #5496bd;
}
.search__button:disabled {
  background-color: #e5e5e5;
}
@media (min-width: 768px) {
  .search__button {
    width: 458px;
    margin-bottom: 58px;
    box-sizing: border-box;
    padding: 17px;
  }
}
@media (min-width: 1200px) {
  .search__button {
    margin-bottom: 66px;
  }
}

.footer {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  flex-shrink: 0;
  width: 320px;
  margin: 0 auto;
  padding-top: 29px;
  text-align: center;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .footer {
    width: 768px;
    padding: 0;
    min-height: 120px;
  }
}
@media (min-width: 768px) and (min-width: 1200px) {
  .footer {
    width: 1200px;
  }
}
.footer--catalog {
  padding-top: 15px;
}
@media (min-width: 768px) {
  .footer--catalog {
    padding: 0;
  }
}
.footer--form {
  padding-top: 0;
}

.footer__link {
  grid-column: 1/-1;
  margin-bottom: 34px;
  font-weight: 700;
  font-size: 21px;
  line-height: 1.2;
  text-transform: uppercase;
  text-decoration: none;
  color: #000000;
}
@media (min-width: 768px) {
  .footer__link {
    grid-column: 1/2;
    margin-top: auto;
    margin-bottom: auto;
    padding: 0;
  }
}
@media (min-width: 1200px) {
  .footer__link {
    margin-right: 26px;
  }
}

.footer__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  grid-column: 1/-1;
  gap: 6px;
  align-items: center;
  margin: 0;
  padding: 0;
  padding-bottom: 40px;
  list-style-type: none;
}
@media (min-width: 768px) {
  .footer__list {
    grid-column: 2/3;
    padding: 0;
  }
}

.footer__copyright {
  display: flex;
  justify-self: center;
  align-items: center;
  align-self: center;
  grid-column: 1/-1;
  margin: 0;
  padding-bottom: 23px;
}
@media (min-width: 768px) {
  .footer__copyright {
    grid-column: 3/4;
    padding: 0;
    margin-right: 24px;
  }
}
@media (min-width: 1200px) {
  .footer__copyright {
    margin: 0;
  }
}
@media (min-width: 768px) {
  .footer__copyright--catalog, .footer__copyright--form {
    margin: 0;
    margin-left: 8px;
  }
}
@media (min-width: 1200px) {
  .footer__copyright--catalog, .footer__copyright--form {
    margin-left: 30px;
  }
}

.catalog {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 37px;
}
@media (min-width: 768px) {
  .catalog {
    margin-top: 66px;
  }
}
@media (min-width: 1200px) {
  .catalog {
    margin-top: 100px;
  }
}

.catalog__wrapper {
  margin: 0 20px 36px;
  text-align: center;
}
@media (min-width: 768px) {
  .catalog__wrapper {
    margin-bottom: 81px;
  }
}
@media (min-width: 1200px) {
  .catalog__wrapper {
    margin-bottom: 86px;
  }
}

.catalog__title {
  margin: 0;
  margin-bottom: 27px;
  font-weight: 700;
  font-size: 18px;
  line-height: 1.4;
  color: #000000;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .catalog__title {
    font-size: 30px;
    line-height: 0.86;
    margin-bottom: 33px;
  }
}

.catalog__description {
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.5;
  text-transform: uppercase;
  color: #333333;
}
.catalog__description br {
  display: none;
}
@media (min-width: 768px) {
  .catalog__description br {
    display: block;
  }
}

.catalog__list {
  display: grid;
  grid-template-columns: 1fr;
  list-style-type: none;
  margin: 0;
  padding: 0;
}
@media (min-width: 768px) {
  .catalog__list {
    grid-template-columns: repeat(2, 1fr);
    padding: 0;
    margin-bottom: 58px;
  }
}
@media (min-width: 1200px) {
  .catalog__list {
    grid-template-columns: repeat(3, 1fr);
    margin-bottom: 59px;
  }
}

.catalog__item {
  display: grid;
  grid-template-columns: 1fr;
  position: relative;
  padding-bottom: 25px;
}
.catalog__item::before {
  content: "";
  position: absolute;
  width: 320px;
  left: 50%;
  border-top: 1px solid #e5e5e5;
  transform: translate(-50%, 0);
}
.catalog__item:last-child::after {
  content: "";
  position: absolute;
  width: 320px;
  bottom: 0;
  left: 50%;
  border-bottom: 1px solid #e5e5e5;
  transform: translate(-50%, 0);
}
@media (min-width: 768px) {
  .catalog__item {
    grid-template-columns: 1fr min-content;
    margin: 0;
    padding: 0;
    background-color: #f2f2f2;
  }
  .catalog__item:nth-child(odd):not(:first-child) {
    background-color: #eaeaea;
  }
  .catalog__item:nth-child(1) {
    grid-column: 1/-1;
  }
  .catalog__item::before, .catalog__item::after {
    display: none;
  }
}
@media (min-width: 1200px) {
  .catalog__item:first-child .catalog__card-title {
    padding-top: 22px;
    padding-left: 21px;
  }
  .catalog__item:first-child .catalog__card-description {
    padding-bottom: 15px;
    padding-left: 21px;
  }
}

@media (min-width: 768px) {
  .catalog__image {
    grid-column: 1/-1;
  }
}
@media (min-width: 1200px) {
  .catalog__image {
    grid-area: 1/1/4/-1;
  }
}

.catalog__card-image {
  display: block;
  margin-top: 25px;
  margin-bottom: 15px;
}
@media (min-width: 768px) {
  .catalog__card-image {
    margin: 0;
  }
}

.catalog__card-title {
  margin: 0;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.5;
  text-transform: uppercase;
  color: #000000;
}
@media (min-width: 768px) {
  .catalog__card-title {
    padding-top: 22px;
    padding-left: 20px;
  }
}
@media (min-width: 1200px) {
  .catalog__card-title {
    grid-area: 2/1/3/2;
    padding-top: 21px;
    padding-left: 25px;
    color: #ffffff;
    background-color: rgba(0, 0, 0, 0.3);
  }
}

.catalog__card-description {
  margin: 0;
  margin-bottom: 11px;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.5;
  color: #333333;
}
@media (min-width: 768px) {
  .catalog__card-description {
    grid-column: 1/2;
    margin: 0;
    padding-bottom: 15px;
    padding-left: 20px;
  }
}
@media (min-width: 1200px) {
  .catalog__card-description {
    grid-area: 3/1/4/2;
    padding-bottom: 17px;
    padding-left: 25px;
    color: #ffffff;
    background-color: rgba(0, 0, 0, 0.3);
  }
}

.catalog__likes-button {
  display: flex;
  padding: 0;
  align-items: baseline;
  width: auto;
  border: none;
  background-color: transparent;
}
.catalog__likes-button::before {
